.home-intro{
    min-height: 100vh;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 0 10rem;
    overflow: hidden;
}
.home-intro_left{
    padding-top: 1rem;
    width: 50%;
}
.home-intro_right{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 50%;
}
.home-intro_title{
    display: flex;
    align-items: center;
    gap: 1em;
}
.home-intro_main_container{
    padding-top: 8rem;
}
.home-intro_main{
    padding-top: 3rem;
    width: 80%;
}
.home-intro_introduce h1{
    font-size: 5vw;
}
.home-intro_state{
    width: fit-content;
    margin-top: 2rem;
    display: flex;
    align-items: center;
    gap: 1rem;
    /* border: 2px dashed var(--black); */
    padding: 0.8rem;
    border-radius: 4rem;
}
.home-intro_state svg{
    height: 40px;
    width: 40px;
}
.intro-me{
    position: relative;
}
.home-intro-me{
    width: 75%;
    height: 85%;
    position: relative;
}
.home-intro-social{
    display: flex;
    /* gap: 1rem; */
    padding-top: 3rem;
}
.home-intro-social svg{
    margin-right: 1rem;

}
.home-intro-me::before{
    background-color: var(--dark-purple);
    background-image: url("../../../asset/image/0.gif");
    background-repeat: round;
    content: '';
    top: 0;
    left: 0;
    position: absolute;
    transform: translate(2rem, 2rem);
    height: 100%;
    width: 100%;
    z-index: -1;
}

@media (max-width: 1200px){
    .home-intro{
        padding: 0 10rem;
    }
    .home-intro-me{
        width: 85%;
    }
}

@media (max-width: 1024px){
    .home-intro{
        padding: 0 5rem;
    }
    .home-intro-me{
        width: 90%;
    }
}

@media (max-width: 768px){
    .home-intro-me{
        width: 100%;
        height: 100%;
    }
    .home-intro_introduce h1{
        font-size: 10vw;
    }
    .home-intro_left,
    .home-intro_right{
        padding-top: 0;
        padding-bottom: 5rem;
    }
    .home-intro_left,
    .home-intro_right{
        width: 100%;
        height: 100%;
    }
    .home-intro{
        flex-direction: column;
    }
    .home-intro_main{
        width: 100%;
    }
    .home-intro_main_container{
        padding-top: 5em;
    }
    .home-intro-me::before{
        display: none;
    }
    .home-intro-me canvas{
        min-height: 100vh;
    }
}

@media (max-width: 480px){
    .home-intro_introduce h1{
        font-size: 12vw;
    }
    .home-intro{
        padding: 0 2rem;
    }
    .home-intro-me{
        width: 100%;
        height: 100%;
    }
    .home-intro_main_container{
        padding-top: 2rem;
    }
    .home-intro_left,
    .home-intro_right{
        width: 100%;
        height: 100%;
    }
    .home-intro_left,
    .home-intro_right{
        padding-bottom: 2rem;
    }
}

@media (max-width: 350px){
    .home-intro_main_container{
        padding-top: 1.3rem;
    }
    .home-intro{
        padding: 0 1.3rem;
    }
    .home-intro_left,
    .home-intro_right{
        padding-bottom: 1.3rem;
    }
}
